<template>
  <div class="registration-form">
    <h2>註冊</h2>
    <form @submit.prevent="register">
      <div class="form-group">
        <label for="nickname">暱稱</label>
        <input type="text" id="nickname" v-model="nickname" required>
      </div>
      <div class="form-group">
        <label for="phone">手機號碼</label>
        <input type="tel" id="phone" v-model="phone" required>
      </div>
      <div class="form-group">
        <label for="email">Email</label>
        <input type="email" id="email" v-model="email" required>
      </div>
      <div class="form-group">
        <label for="password">密碼</label>
        <input type="password" id="password" v-model="password" required>
      </div>
      <div class="form-group">
        <label for="coverPhoto">封面照片</label>
        <input type="file" id="coverPhoto" accept="image/*" @change="handleCoverPhotoChange">
      </div>
      <div class="form-group">
        <label for="introduction">自我介紹</label>
        <textarea id="introduction" v-model="introduction" rows="5"></textarea>
      </div>
      <div class="button-group">
        <button type="button" @click="goBack">返回前頁</button>
        <button type="submit">提交</button>
      </div>
    </form>
    <Modal v-if="showModal" title="註冊成功!" content="請點擊確認按鈕跳轉至登入頁" button="確認" @close-and-redirect="redirectToLogin" />
  </div>
</template>

<script>
import Modal from '../components/Modal.vue';

export default {
  components: {
    Modal
  },
  data() {
    return {
      nickname: '',
      phone: '',
      email: '',
      password: '',
      coverPhoto: null,
      introduction: '',
      showModal: false 
    };
  },
  methods: {
    register() {
      const formData = {
        nickname: this.nickname,
        phone: this.phone,
        email: this.email,
        password: this.password,
        coverPhoto: this.coverPhoto,
        introduction: this.introduction
      };
      this.showModal = true;
    },
    redirectToLogin() {
      this.$router.push({ name: 'UserLogin', query: { source: 'registration' } });
    },
    handleCoverPhotoChange(event) {
      this.coverPhoto = event.target.files[0];
    },
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>