<template>
  <div class="login-container">
    <h2>登入</h2>
    <form @submit.prevent="login">
      <div class="form-group">
        <label for="phone">手機號碼</label>
        <input type="tel" id="phone" v-model="phone" required>
      </div>
      <div class="form-group">
        <label for="password">密碼</label>
        <input type="password" id="password" v-model="password" required>
      </div>
      <div class="button-group">
        <button v-if="showBackButton" type="button" @click="goBack">返回</button>
        <button type="submit">登入</button>
      </div>
    </form>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  data() {
    return {
      phone: '',
      password: '',
      showBackButton: false // 是否顯示返回按鈕的標誌
    };
  },
  methods: {
    ...mapActions(["doUserLogin"]),
    login() {
      const phone = this.phone;
      const password = this.password;

      // call API-登入
      this.doUserLogin({
        phone,
        password
      });
      this.$router.push({ name: 'Home' });
    },
    goBack() {
      // 返回前頁的相關邏輯，可以使用 Vue Router 的 $router.go(-1) 方法返回前一頁
      this.$router.go(-1);
    }
  },
  created() {
    // 檢查是否從註冊畫面導航過來
    if (this.$route.query.source === 'registration') {
      this.showBackButton = false; // 不顯示返回按鈕
    } else {
      this.showBackButton = true; // 顯示返回按鈕
    }
  }
};
</script>