import { createRouter, createWebHashHistory } from 'vue-router'
import LoginScreenPage from '../views/LoginScreenPage.vue'
import DetailPage from '../components/DetailPage.vue'
import UserLoginPage from '../components/UserLoginPage.vue'
import RegistrationPage from '../components/RegistrationPage.vue'
import HomePage from '../views/HomePage.vue'

const routes = [
  {
    path: '/',
    name: 'loginScreen',
    component: LoginScreenPage // 初始畫面 (登入前)
  },
  {
    path: '/userLogin',
    name: 'UserLogin',
    component: UserLoginPage, // 登入頁面
  },
  {
    path: '/registration',
    name: 'Registration',
    component: RegistrationPage, // 註冊頁面
    props: true
  },
  {
    path: '/home',
    name: 'Home',
    component: HomePage, // 登入後首頁
  },
  {
    path: '/Detail/:id',
    name: 'Detail',
    component: DetailPage, // 查看貼文頁面
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
