import axios from 'axios';

const state = {
  posts: []
};

const getters = {
  allPosts: state => state.posts
};

const fetchWrapper = async (url, requestData) => {
  try {
    return axios.post(url, requestData);
  } catch (error) {
    console.error('Error posting data to API:', error);
  }
};

const actions = {
  async doRegister({ onSuccess }) {     // test
    // 設置 API 的 URL
    const url = 'http://your-internal-api-url.com/data';
    // 設置 POST 請求的請求體（request body）
    const requestData = {
      key: 'value'
    };

    const [responseData] = await fetchWrapper(url, requestData);

    onSuccess && onSuccess(responseData);
  },

  async doUserLogin({ commit }, userInfo) {
    const { phone, password } = userInfo;
    const baseURL = 'http://localhost:8080';
    const headers = {
      'Accept': 'application/json, text/plain, */*',
      'Authorization': 'Basic ' + btoa(phone + ':' + password),
      'Content-Type': 'application/json'
    };

    const instance = axios.create({
      baseURL: baseURL,
      headers: headers,
      timeout: 2000 
    });

    try {
      const response = await instance.post('/userLogin');
      console.log('Response:', response.data);
    } catch (error) {
      console.error('Error posting data to API:', error);
    }
  },

  async fetchPosts({ commit }) {       //fetch API               
    // const response = await axios.get(
    //   'https://jsonplaceholder.typicode.com/comments'
    // );
    const responseData = [
      {
        "postId": 1,
        "userId": 1,
        "content": "Good morning everyone! How's your day going?",
        "image": "morning.jpg",
        "createdAt": "2024-04-24T17:26:56.000+00:00"
      },
      {
        "postId": 2,
        "userId": 1,
        "content": "quo vero reiciendis velit similique earum",
        "image": "dog.jpg",
        "createdAt": "2024-04-25T18:26:56.000+00:00"
      },
      {
        "postId": 3,
        "userId": 1,
        "content": "non et atque\noccaecati deserunt quas accusantium unde odit nobis qui voluptatem\nquia voluptas consequuntur itaque dolor\net qui rerum deleniti ut occaecati",
        "image": "cat.jpg",
        "createdAt": "2024-04-25T22:26:56.000+00:00"
      }
    ];
    console.log(responseData);

    commit('setPosts', responseData);
  },

  async deletePost({ commit }, id) {   //delete API data according to id 
    await axios.delete(`https://jsonplaceholder.typicode.com/comments/${id}`);

    commit('removePost', id);
  },

  async filterPosts({ commit }, e) {   //filter API data according to e (keyboard key in value)
    // Get search number
    console.log(e);
    const limit = parseInt(
      parseInt(e.target.value)
    );

    const response = await axios.get(
      `https://jsonplaceholder.typicode.com/comments?_limit=${limit}`
    );

    commit('setPosts', response.data);
  },

  async updatePost({ commit }, updPost) {  //update API data  
    const response = await axios.put(
      `https://jsonplaceholder.typicode.com/comments/${updPost.id}`,
      updPost
    );

    console.log(response.data);

    commit('updatePosts', response.data);
  }
};

const mutations = {
  setPosts: (state, posts) => (state.posts = posts),

  removePost: (state, id) =>
    (state.posts = state.posts.filter(post => post.id !== id)),

  updatePosts: (state, updPost) => {
    const index = state.posts.findIndex(post => post.id === updPost.id);
    if (index !== -1) {
      state.posts.splice(index, 1, updPost);
    }
  }
};

export default {
  state,
  getters,
  actions,
  mutations
};