<template>
    <div>
        <!-- 點擊觸發燈箱顯示 -->
        <div @click="showModal = true">
            <slot></slot>
        </div>

        <!-- 燈箱模態框 -->
        <div v-if="showModal" class="Modal">
            <!-- 燈箱內容 -->
            <div class="Modal-content">
                <!-- 標題 -->
                <h2>{{ title }}</h2>
                <!-- 內容 -->
                <p>{{ content }}</p>
                <!-- 關閉按鈕 -->
                <div class="close-button-container">
                    <button class="close-button" @click="closeModal">{{ button }}</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        title: String,
        content: String,
        button: String
    },
    data() {
        return {
            showModal: true // 默認顯示燈箱
        };
    },
    methods: {
        closeModal() {
            // 觸發自定義事件，在組件外部監聽該事件並執行跳轉到登入頁面的操作
            this.$emit('close-and-redirect');
            // 關閉燈箱
            this.showModal = false;
        }
    }
};
</script>
